@import "../../assets/styles/axis-style-override";
.control-panel {
    flex: 1 auto;
    order: 1;
    overflow-y: scroll;
    max-width: 540px;
    height: 100vh;
    border: 1px solid $border-color;
    .ant-card-head {
        border-bottom: none;
  }
  .ant-card-meta {
      margin: 0px;
      border-bottom: 1px solid $border-color;
  }
  .ant-card-meta-title {
    margin: 0px;
    padding: 0px 24px 16px 24px;
  }
  .ant-card-body {
    padding: 0;
  }
  .ant-card-extra .ant-btn {
    margin-right: 1em;
  }
  .ant-collapse-item .ant-collapse-header {
    padding: 0;
  }
  //slider settings
  @extend %axis-override;
}




